import React from 'react';
import ReactDOM from 'react-dom';
import { HashRouter } from 'react-router-dom';
import './index.css';
import App from './App';
import registerServiceWorker from './registerServiceWorker';

ReactDOM.render(
  <HashRouter>
    <App />
  </HashRouter>, 
  // <BrowserRouter>
  //   <App />
  // </BrowserRouter>, 
  document.getElementById('root'));

registerServiceWorker();


